<template>
  <li class="blogitem" @click="readblog">
    <h4 class="blogitem-title">{{item.title}}</h4>
    <p class="blogitem-time">
      {{item.time}}
      <el-tag v-for="tag in item.tag" :key="tag" :type="''" :size="'mini'">{{tag}}</el-tag>
    </p>
    <p class="blogitem-texttop">{{item.texttop}}</p>
  </li>
</template>

<script>
export default {
  name: 'blogItem',
  props: ['item'],
  methods: {
    readblog () {
      this.$router.push(`/blog/list/${this.item.id}`)
    }
  }
}
</script>

<style lang="less" scoped>
  .blogitem-color{
    color: #fff;
    -webkit-text-shadow: 0 0 10px #333;
    -moz-text-shadow: 0 0 10px #333;
    text-shadow: 0 0 10px #333;
  }
  .textnowarp{
    white-space: nowrap;
    overflow: hidden;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
  }
  .blogitem{
    font-size: 12px;
    background-color: #404848;/*#607D8B*/
    overflow: hidden;
    margin-bottom: 1px;
    padding-bottom: 10px;
    cursor: pointer;
    transition: ease font-size 0.5s;
  }
  .blogitem:hover{
    font-size: 14px;
  }
  .blogitem .blogitem-title{
    border-left:10px solid #02a8f5;
    margin: 6px;
    text-align: left;
    padding-left: 4px;
    font-size: 1.2em;
    .blogitem-color;
    .textnowarp;
  }
  .blogitem .blogitem-time,.blogitem-texttop{
    text-align: left;
    padding-left: 20px;
    font-size: .9em;
    .blogitem-color;
    .textnowarp;
  }
  .blogitem .el-tag{
    margin: 4px;
    cursor: pointer;
  }
</style>



