<template>
  <Main class="">
    <header class="blog-header">
      <ul>
        <li class="item">
          <router-link to="/home">Home</router-link>
        </li>
        <li class="item">
          <router-link to="/blog/list">List</router-link>
        </li>
        <li class="item">
          <a href="http://129.204.39.187" target="_blank">Cinema</a>
        </li>
      </ul>
    </header>
    <Main class="blog-con">
      <section class="blog-con-left">
        <div class="blog-con-left-header">
          <span class="head"></span>
          <p style="margin: 5px;color: #098e8e">只要有斗志，不怕没战场。</p>
          <p style="color: #071eee;font-family: -webkit-pictograph;text-shadow: 0 0 10px #555">As long as there is fighting spirit, we are not afraid of no battlefield.</p>
        </div>
        <div class="blog-con-left-con">
          <el-tag v-for="tagclassitem in tagclass" :type="rgb[tagclassitem.color]" @click="search(tagclassitem.name)">{{tagclassitem.name}}({{tagclassitem.count}})</el-tag>
        </div>
      </section>
      <section class="blog-con-right">
        <el-carousel :interval="4000" type="card" height="200px" v-if="flagCarousel">
          <!--<el-carousel-item v-for="item in carousel" :key="item">-->
            <!--<img :src="item.src"/>-->
            <!--<h3>{{ item.title }}</h3>-->
          <!--</el-carousel-item>-->
          <el-carousel-item @click.native="$router.push('/blog/list/1')">
            <img src="./../assets/images/carousel/1.jpg"/>
            <h3>nodejs爬虫</h3>
          </el-carousel-item>
          <el-carousel-item  @click.native="$router.push('/blog/list/2')">
            <img src="./../assets/images/carousel/2.jpg"/>
            <h3>js运行机制</h3>
          </el-carousel-item>
          <el-carousel-item  @click.native="$router.push('/blog/list/3')">
            <img src="./../assets/images/carousel/3.jpg"/>
            <h3>浏览器渲染机制</h3>
          </el-carousel-item>
        </el-carousel>
        <ul class="blog-con-right-con" style="margin-top: 4px;">
          <div class="yxd-loading" :style="flagLoading"></div>
          <blog-item v-for="item in carousel" :item="item"></blog-item>
        </ul>

      </section>
      <div style="clear: both;"></div>
    </Main>
    <footer class="blog-footer">
      <span>vue @2.5.2</span>
      <span>elementUi @2.6.1</span>
    </footer>
  </Main>
</template>

<script>
import BlogItem from '@/components/BlogItem.vue'
import BlogShow from '@/components/BlogShow.vue'
import { mapMutations, mapState } from 'vuex'
export default {
  name: 'blog',
  components: {BlogItem, BlogShow},
  data () {
    return {
      flagCarousel: false,/* 轮播显示隐藏*/
      flagLoading: 'height:50px;display:block;',
      showItem: {},
      rgb: ['', 'success', 'info', 'warning', 'danger'],
      carousel: [],//显示列表
      searchBlog: [],
      tagclass: [
        {name:'All', color:4, count:3}, {name:'html', color:0, count:1}, {name:'css', color:1, count:0}, {name:'js', color:2, count:1}, {name:'vue', color:3, count:0}, {name:'nodejs', color:4, count:1},
        {name:'php', color:0, count:0}, {name:'Asp.net', color:1, count:0}, {name:'sqlserver', color:2, count:0}, {name:'mysql', color:3, count:0}, {name:'Android', color:4, count:0},
        {name:'elementui', color:0, count:0}, {name:'mintui', color:1, count:0}, {name:'python', color:2, count:0}, {name:'java', color:3, count:0}, {name:'smail', color:4, count:0}
      ],
      allBlogArr:[]
    }
  },
  methods: {
    ...mapMutations ([
      'setAllBlog'
    ]),
    showBlog (id) {
      let arr = this.carousel
      for (let item in arr) {
        if (arr[item].id == id) {
          this.showItem = arr[item]
          return true
        }
      }
      return false
    },
    getDateTimes (date) {/* date 日期格式 2019-01-01 String */
      return new Date(date).getTime()
    },
    arrJsonSort (arr, cond, desc) {/* 可排序格式[{}] params:arr,Array(需要排序的数组) .... cond,string(条件) ... desc,number(-1倒序,1顺序) */
      arr.sort((a, b) => {
          return desc*(this.getDateTimes(a[cond]) - this.getDateTimes(b[cond]))
      })
    },
    getCarousel () {
      let arr = this.allBlog
      if (arr.length == 0) {
        this.$http.get('https://api.myjson.com/bins/14dwpm')//'https://api.myjson.com/bins/vjdw2'
          .then((data)=>{
            if (data.status == 200){
              this.allBlogArr = data.data
              this.arrJsonSort(this.allBlogArr, 'time', -1)
              this.carousel = this.allBlogArr.slice(0,5)
              this.flagLoading = 'height:0px;'
              setTimeout(()=>{this.flagLoading = 'display:none'},300)
              this.setAllBlog(data.data)
              this.flagCarousel = true
              // this.$store.commit('setAllBlog',data.data)
            }
          })
      }else {
        this.allBlogArr = arr
        this.flagLoading = 'display:none'
        this.carousel = this.allBlogArr.slice(0,5)
        this.flagCarousel = true
      }

    },
    search (name) {
      let arr = this.allBlog
      if (name == 'All') {
        this.searchBlog = arr
        this.carousel = this.searchBlog.slice(0,5)
        return
      }
      let len = arr.length
      this.searchBlog = []
      // debugger
      for (let i = 0; i < len; i++) {
        if (arr[i]['tag'].indexOf(name) != -1) {
          this.searchBlog.push(this.allBlogArr[i])
        }
      }
      this.carousel = this.searchBlog.slice(0,5)
    }
  },
  created () {
  },
  mounted () {
    this.getCarousel()
    let id = this.$router.history.current.params.id
    if (id) {
      this.flagCarousel = false
      !this.showBlog(id)&&this.$router.push('/blog')
    }
  },
  computed: {
    ...mapState(['allBlog'])
  },
  watch: {
    '$route' (to, from) {
      // 对路由变化作出响应...
      let id = to.params.id
      if (id) {
        !this.showBlog(id)&&this.$router.push('/blog')
      }
    }
  }
}
</script>

<style scoped lang="less">
  .blog-header, .blog-footer {
    background-color: #00BCD4;
    color: #f7f7f7;
    text-align: center;
    line-height: 60px;
    opacity: 0.7;
  }
  .blog-header ul{
    overflow: hidden;
    margin: 0 auto;
  }
  .blog-header .item{
    min-width: 60px;
    float: left;
    list-style: none;
  }
  .blog-con{
    margin: 10px auto 10px;
    min-height: 602px;
    background-color: #333;
    opacity: 0.8;
  }
  .blog-con-left,.blog-con-right{
    float: left;
    min-height: 800px;
  }
  .blog-con-left{
    width: 35%;
    /*min-height: 500px;*/
    box-shadow: 0 0px 10px 0 #ccc;
  }
  .blog-con-left-header{
    height: 200px;
    overflow: hidden;
    background: #05192e;
  }
  .blog-con-left-header .head{
    height: 80px;
    width: 80px;
    margin-top: 25px;
    display: inline-block;
    border-radius: 50%;
    background-color: #222222;
    background-image: url("./../assets/images/head.jpeg");
    background-position: 50% 50%;
    background-size: 100%;
  }
  .blog-con-left-con{
    margin-top: 20px;
  }
  .blog-con-right{
    margin-left: 1%;
    width: 63%;
  }
  .blog-footer span{
    margin: 4px;
  }
  .el-carousel__item h3 {
    width: 100%;
    position: absolute;
    bottom: 10px;
    font-size: 16px;
    color: #fff;
    -webkit-text-shadow: 0 0 10px #333;
    -moz-text-shadow: 0 0 10px #333;
    text-shadow: 0 0 10px #333;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }

  .el-tag{
    margin: 4px;
    cursor: pointer;
  }
  @media screen and (min-width: 1400px){
    .blog-con,.blog-header ul{
      width: 1200px;
    }
  }
  @media screen and (min-width: 1000px) and (max-width: 1400px){
    .blog-con,.blog-header ul{
      width: 800px;
    }
  }
  @media screen and (max-width: 600px){
    .blog-con,.blog-header ul{
      width: 100%;
    }
    .blog-con-left{
      display: none;
    }
    .blog-con-right{
      width: 100%;
      margin-left: 0;
      padding: 0 1px;
    }
    .el-carousel__item--card.is-active{
      width: 100% !important;
      transform: translateX(0) scale(1) !important;
    }
    .el-carousel__item:nth-child(n) {
      /*width: 0;*/
    }
  }
  @keyframes yxdloading
  {
    0% {
      background:red;
      left: 20%;
      transform: translate(-50%,-50%) scale(0.5) rotate(360deg);
    }
    50% {
      left: 50%;
      background:yellow;
      transform: translate(-50%,-50%) scale(0.8) rotate(-360deg);
    }
    100% {
      background:red;
      left: 80%;
      transform: translate(-50%,-50%) scale(0.5) rotate(360deg);
    }
  }
  .yxd-loading{
    height: 50px;
    /*display: none;*/
    background: #222;
    position: relative;
    transition: ease height 0.5s;
  }
  .yxd-loading:before{
    content: '';
    position: absolute;
    top: 55%;
    left: 50%;
    padding: 5px;
    animation: yxdloading 2s ease infinite alternate-reverse;
  }
</style>



